import MainHeader from "@/components/MainHeader/MainHeader.vue";
import SearchBlock from "@/components/SearchBlock/SearchBlock.vue";
import { mapActions } from "vuex";

export default {
  components: {
    MainHeader,
    SearchBlock,
  },
  methods: {
    ...mapActions({
      setHeaderLight: "Header/setHeaderLight",
    }),
  },
  created() {
    this.setHeaderLight();
  },
};
